tero proceso de servicio y atención al ciudadano.")]),_c('p',[_vm._v("La fundamentación de la NTC GP 1000 como es convencional en las normas ISO, busca transformar y disciplinar las prácticas, en este caso, la gestión de las organizaciones públicas, en un sistema basado en procesos, promueve y no meramente por funciones y verticalidades burocráticas. Busca determinar y gestionar series de actividades relacionadas entre sí hasta su producto o entregable. La ventaja de este enfoque es que facilita tener un control y trazabilidad y hacer continuas las operaciones individuales y de procesos de la entidad cómo un todo. El enfoque permite entender el funcionamiento de la institución como un conjunto de mecanismos articulados mejorando la dinámica y las sinergias entre actividades relacionadas facilitando a su vez el mejoramiento continuo.")]),_c('div',{staticClass:"tarjeta color-primario p-3 mb-5"},[_c('div',{staticClass:"row justify-content-around align-items-center"},[_vm._m(42),_c('div',{staticClass:"col"},[_c('div',{staticClass:"row justify-content-between align-items-center"},[_vm._m(43),_c('div',{staticClass:"col-sm-auto"},[_c('a',{staticClass:"boton color-acento-botones",attrs:{"href":_vm.obtenerLink('/downloads/1_Anexo-NORMA TÉCNICA DE CALIDAD EN LA GESTIÓN PÚBLICA-NTC GP1000-2009.pdf'),"target":"_blank"}},[_c('span',[_vm._v("Descargar")]),_c('i',{staticClass:"fas fa-file-download"})])])])])])])])]),_c('p',{staticClass:"mb-5"},[_vm._v("Las ventajas en la gestión institucional permiten: mejorar la comprensión y el cumplimiento de los requisitos de los ciudadanos, considerar los procesos como generadores de valor social o económico, obtener resultados de desempeño y eficacia fácilmente, proponer mejora continua de los procesos tomando como referencia las mediciones o métricas de actuación.")]),_vm._m(44),_c('p',{staticClass:"mb-5"},[_vm._v("La NTC GP 1000-2009 establece que los principios del Sistema de Gestión de Calidad de las entidades públicas se enmarquen, inte